import { Component, OnInit, ChangeDetectionStrategy } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { themeType } from '../enums/theme.enum';
import { ConfigurationService } from '../services/configuration.service';

@Component({
  selector: 'app-auth',
  templateUrl: './auth.component.html',
  styleUrls: ['./auth.component.scss']
})
export class AuthComponent implements OnInit {

  activeComp = "signup";
  isCustomLogin = false;
  inputObj: any = {
    isCustomLogin: false,
    profileName: null,
    themeConfig: null,
    isDraft: false
  };
  preloaded = null;
  constructor(
    private route: ActivatedRoute,
    private router: Router,
    private configService: ConfigurationService
  ) {

    const routeParams = this.route.snapshot.paramMap;
    this.inputObj.profileName = routeParams.get('parentId');
    if (this.inputObj.profileName) {
      this.activeComp = "login";
      this.inputObj.isCustomLogin = true;
      localStorage.setItem("theme", "custom");
      this.inputObj.isDraft = this.router.url.toLowerCase().includes('preview');


      this.route.queryParams.subscribe(params => {
        this.preloaded = params['theme'];
        this.getTheme();
      });


    } else {
      localStorage.setItem("theme", "default");
      localStorage.removeItem("themeConfig");
    }
  }
  ngOnInit(): void {
  }

  onToggle(section: any) {
    this.activeComp = '';
    setTimeout(() => {
      this.activeComp = section;
    })
  }

  getTheme() {

    if (this.inputObj.isDraft && !this.preloaded) {
      const type = this.inputObj.isDraft ? themeType.draft : themeType.published;
      this.configService.getTheme(this.inputObj.profileName, type)
        .subscribe((res: any) => {
          console.log("getTheme - success : ", res);

          if (res.error && res.error.responseCode === 0) {
            this.inputObj.themeConfig = res.profileData;
          }
          localStorage.setItem("themeConfig", JSON.stringify(this.inputObj.themeConfig));
        }, (err) => {
          console.log("getTheme - error:", err);
        })
    } else {
      this.inputObj.themeConfig = {
        "logo": "https://www.google.com/images/branding/googlelogo/1x/googlelogo_color_272x92dp.png",
        "login": {
          "text": "Welcome to Impairbit",
          "backgroundImage": "https://cdn.wallpapersafari.com/59/94/g7KCNA.jpg",
          "bgcolor": "#0f87c2",
          "textColor": "#ea4335",
          "labelColor": "#34a853",
          "buttonColor": "#fbbc05",
          "buttonTextColor": "#fff"
        },
        "menu": {
          "bgcolor": "#0f87c2",
          "textColor": "#fff",
          "activeBgColor": "#fff",
          "activeTextColor": "#0f87c2"
        }
      }
      localStorage.setItem("themeConfig",JSON.stringify(this.inputObj.themeConfig));
    }
  }
}
